import React from 'react';
import style from "./Paginator.module.css"

let Paginator = ({currentPage, totalItemsCount, pageSize, onPageChanged, portionSize = 10}) => {
    let pagesCount = Math.ceil(totalItemsCount / pageSize);
    let pages = [];
    if (pagesCount > portionSize) {
        let leftPos = currentPage - Math.ceil(portionSize / 2);
        let rightPos = leftPos + portionSize - 1;
        if (leftPos <= 0) {
            rightPos = portionSize;
            leftPos = 1;
        }
        if (rightPos > pagesCount) {
            leftPos = pagesCount - portionSize + 1;
            rightPos = pagesCount;
        }
        for (; leftPos <= rightPos; leftPos++) {
            pages.push(leftPos);
        }
    } else {
        for (let i = 1; i <= pagesCount; i++) {
            pages.push(i);
        }

    }
    return (
        <div>
            <ul className={style.pagination}>
                {/*{pages[0] != 1 && <a onClick={(e) => {
                    onPageChanged((currentPage - portionSize) <= 0 ? 1 : currentPage - portionSize)
                }}>{"<<"}</a>}*/}
                {pages[0] != 1 && <a onClick={(e) => {
                    onPageChanged(1)
                }}>{1}</a>}
                {pages[0] != 1 && <a className={style.fake}>{"-"}</a>}


                {pages.map(p => {
                    return <a key={p.id} className={currentPage === p && style.active}
                              onClick={(e) => {
                                  onPageChanged(p);
                              }}>{p}</a>
                })}


                {pages[9] != pagesCount && <a className={style.fake}>{"-"}</a>}
                {pages[9] != pagesCount && <a onClick={(e) => {
                    onPageChanged(pagesCount)
                }}>{pagesCount}</a>}
                {/*{pages[9] != pagesCount && <a onClick={(e) => {
                    onPageChanged(((currentPage + portionSize) > pagesCount ? pagesCount : currentPage + portionSize))
                }}>{`>>`}</a>}*/}

            </ul>
        </div>
    )

}

export default Paginator;