const SEND_MESSAGE = 'dialogs/SEND_MESSAGE';
const UPDATE_NEW_MESSAGE_BODY = 'dialogs/UPDATE-NEW-MESSAGE_BODY';

let initialState = {
    dialogs: [
        {id: 1, name: 'Dimych'},
        {id: 2, name: 'Andrey'},
        {id: 3, name: 'Sveta'},
        {id: 4, name: 'Sasha'},
        {id: 5, name: 'Victor'},
        {id: 6, name: 'Valera'},
    ],
    messages: [
        {id: 1, message: 'Hi'},
        {id: 2, message: 'How is your it?'},
        {id: 3, message: 'Yo'},
        {id: 4, message: 'Ha'},
        {id: 5, message: 'Yo'},
        {id: 6, message: 'Ya'},
    ]
}

const dialogsReducer = (state = initialState, action) => {

    let stateCopy;
    switch (action.type) {
        case SEND_MESSAGE:
            stateCopy = {
                ...state,
                messages: [...state.messages, {id: 7, message: action.newMessageBody}]
            };
            return stateCopy;
        default:
            return state;

    }
}

export const sendMessageCreator = (newMessageBody) => ({type: SEND_MESSAGE, newMessageBody});

export default dialogsReducer;